class HomePage extends Component
{

    constructor(props)
    {
        super(props);
        this.state = {
            open:false,
            itemCode:"",
            value:0
        }
    }
    handleChange = (event, value) => {
        this.setState({ value });
      };
    
    openPopup = (newItemCode) =>
    {
        this.setState({
            open:!this.state.open,
            itemCode:newItemCode
        });
        console.log(this.state.url);
    }
    closePopup = () =>
    {
        this.setState({
            open:false
        })
    }
    render()
    {

        const cards =  items.map((item,i) => (
            <Grid item xs={6} sm={3}>
                    <Card className={this.props.classes.card} raised="true">
                        <CardHeader 
                            title={item.title}
                            subheader={item.subheader}
                        />
                        <CardActionArea onClick = {e => this.openPopup(item.code)}>
                            <CardMedia >
                                <Image isCropped = {this.state.close} code={item.code} />
                            </CardMedia>
                        </CardActionArea>
                        <CardContent>
                            <h1>
                                Price:{item.price}
                            </h1>
                        </CardContent>
                    </Card>
            </Grid>
        ))
        return (
            
          <div className={this.props.classes.root}>
           <AppBar position='static' className={this.props.classes.appBar}>
                <div className={this.props.classes.appTitle}>
                    Try to Learn Shop
                </div>
            </AppBar>
            <Grid container spacing={3}>
                {cards}
             </Grid>
             <Dialog open={this.state.open}>
                <DialogTitle>
                    <IconButton aria-label="close" className={this.props.classes.closeButton} onClick={this.closePopup}>
                        <CloseIcon />
                    </IconButton>
                </DialogTitle>
                <DialogContent>
                    <Image isCropped = {this.state.open} code={this.state.itemCode} />
                </DialogContent>
            </Dialog>
          </div>   
         );
    }
}
 export default withStyles(styles)(HomePage);